import Vue from 'vue'
import Router from 'vue-router'

// Containers
const Container = () => import('@/components/containers/Container')

// Main pages
const Home = () => import('@/components/Home')
const About = () => import('@/components/About')
const Work = () => import('@/components/Work')
const Contact = () => import('@/components/Contact')

// Main pages - Work
const TMS = () => import('@/components/work/TMS')
const BuyerPortal = () => import('@/components/work/BuyerPortal')
const Fulfillment = () => import('@/components/work/Fulfillment')
const Returnman = () => import('@/components/work/Returnman')
const Shippingman = () => import('@/components/work/Shippingman')

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      redirect: '/home',
      name: '',
      component: Container,
      children: [
        {
          path: 'home',
          name: 'Home',
          component: Home
        },
        {
          path: 'about',
          name: 'About',
          component: About
        },
        {
          path: 'work',
          name: 'Work',
          component: Work
        },
        {
          path: 'tms',
          component: TMS
        },
        {
          path: 'buyerportal',
          component: BuyerPortal
        },
        {
          path: 'fulfillment',
          component: Fulfillment
        },
        {
          path: 'returnman',
          component: Returnman
        },
        {
          path: 'shippingman',
          component: Shippingman
        },
        {
          path: 'contact',
          name: 'Contact',
          component: Contact
        }
      ]
    }
    /*        {
                path: '/about',
                name: 'about',
                // route level code-splitting
                // this generates a separate chunk (about.[hash].js) for this route
                // which is lazy-loaded when the route is visited.
                component: () => import(/!* webpackChunkName: "about" *!/ '../views/About.vue')
            }*/
  ]
})
